@import "/styles/utils/variables.scss";
@import "/styles/utils/extends.scss";
@import "/styles/utils/mixins.scss";
@import "/styles/blocks/stopWatch.scss";

body {
  @extend %decoration;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
